import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Button from '@material-ui/core/Button'
import Link from './Link'

const editWarning = (MyLink) => (
  <>
    <p> OI Wiki </p>
    <p>
      
      <MyLink to="/intro/htc/" target="_blank" rel="noopener noreferrer nofollow"></MyLink>
      
    </p>
    <p></p>
  </>
)
function EditWarn ({ relativePath, dialogOpen, setDialogOpen, location }) {
  const editURL = 'https://github.com/OI-wiki/OI-wiki/edit/master/docs/'
  return (
    <Dialog
      open={dialogOpen}
      onClose={() => {
        setDialogOpen(false)
      }}
    >
      <DialogTitle></DialogTitle>
      <DialogContent>{editWarning(Link(location))}</DialogContent>
      <DialogActions>
        <Button
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
        <Button
          component="a"
          href={editURL + relativePath}
          target="_blank"
          rel="noopener noreferrer nofollow"
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default EditWarn
